
import { Player } from '@/classes/player.class'
import { Options, Vue } from 'vue-class-component'

@Options({
  props: {
    player: Player
  }
})
export default class PlayerButtons extends Vue {
  player!: Player

  get paused () {
    return this.player.paused.value
  }

  togglePaused () {
    this.player.togglePaused()
  }
}
